@media screen and (max-width: 744px) {
	header{
		float:none;
		clear:both;
		border-top: none;
	}

	div.inner {
		height: auto;
		margin: 0 auto;
		padding: 0 1%;
		text-align: center;
		width: 98%;
		min-width:50%;
	}

	h1{
		text-align: center;
	}

	.hd_right{float: none; width: 100%;}
	.logo img{float: none;}

	.resizeImg{
		width: 100%;
	}
	.phone_sp{
		display:inline-block;
		color:#333;
	}
	.phone_pc{
		display:none;
	}

	div.banner{
		display: none;
	}

	.dl_noti dd{
		padding-left:80px;
	}

	.img1_ct,.img2_ct,.img3_ct{
		float:none;
		width:100%;
		max-width:426px;
	}
	.img2_ct,.img3_ct{
		max-width:495px;
	}
	.box_blue{
		padding:15px 10px;
	}
	.box_left,{
		float:none;
		width:100%;
		margin-top:10px;
	}
	.box_right{
		float:none;
		width:100%;
		margin-top:10px;
		table{
			margin-top:15px;
			td{
				padding-right:5px;
				img{
					width:100%;
				}
			}
		}
	}
	.left_map{
		float:none;
		width:100%;
		max-width:292px;
		margin-left:0;
		margin-bottom:10px;
	}
	.box_right_02{
		margin:0 auto;
		text-align:left;
		float:none;
		width:100%;
		max-width:375px;
	}

	footer{
		.ft_left{
			float:none;
			width:100%;
			margin-bottom:20px;
		}
		.ft_right{
			float:none;
			width:100%;
			margin-bottom:0;
		}
	}

}
